#root {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 20px;
  background-color: #7f5a83;
background-image: linear-gradient(315deg, #7f5a83 0%, #0d324d 74%);

}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html{
  color: #777;
}
.container{
  transform: translateY(-100px);
  width: 90%;
  max-width: 500px;
  background-color: #FFF;
  padding: 15px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 20px 30px rgba(0, 0, 0, 0.322);
}
h1{
  font-weight: lighter;
  text-transform: uppercase;
  margin-bottom: 30px;
}
form{
  display: flex;
  flex-direction: column;
  width: 100%;
}

label{

}

textarea{
  height: 150px;
  padding: 8px 12px;
  border: none;
  outline: none;
  background-color: rgb(235, 235, 235);
  margin-bottom: 20px;
  border-radius: 3px;
  font-family: inherit;
  width: 100%;
}

button{
  background-color: #20BF55;
  padding: 8px 12px;
  border: none;
  cursor: pointer;
  color: #FFF;
  border-radius: 99px;
  margin-bottom: 40px;
  transition: .3s;
}

button:hover{
  background-color: #65f093;
}
a{
  display: inline-block;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
